  status: string;
  price: string; // ÂæåÁ´ØÂèØËÉΩËøîÂõûÂ≠óÁ¨¶‰∏≤ÔºåÈúÄÂú®ÂâçÁ´ØËΩâÁÇ∫Êï∏Â≠ó
  stock: number;
